import { HttpClient } from '@angular/common/http';
import { Component, OnDestroy, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { LoadingBarService } from '@ngx-loading-bar/core';
import { MyHttpService } from '../my-http.service';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
export class LoginComponent implements OnInit,OnDestroy {

  EMAIL_PATTERN = "^[_A-Za-z0-9-\\+]+(\\.[_A-Za-z0-9-]+)*@"
  + "[A-Za-z0-9-]+(\\.[A-Za-z0-9]+)*(\\.[A-Za-z]{2,})$";

  incorrect:string='';
  dataresponse:any={};
  ngOnInit(){
    sessionStorage.clear();
    document.body.className="bg-img";
  }
  ngOnDestroy(): void {
    document.body.className="";
  }
  constructor(private routes:Router,private service:MyHttpService,private loading:LoadingBarService) { }
 // constructor(private service:MyHttpService) { }
 msg:string='';
 
  onSubmit(f){
    console.log(f);
    console.log(f.value.email);
    console.log(f.value.password);
    this.loading.start();
    let obj={
      emailid:f.value.email,
      password:f.value.password
    }
   

   this.service.postLogin(obj)
    .subscribe((response)=>{
             console.log(response)
             this.dataresponse=(<any>response);
             this.loading.stop();
             if(this.dataresponse.msg=="Valid User"){
              sessionStorage.setItem("username",this.dataresponse.userobj.name);
              this.routes.navigate(["/home"]);
              
             }else{
               this.incorrect="Invalid user"
               this.routes.navigate(["/login"])
             }   
   })
   
  
 
  }

}
