import { ActivatedRoute, ActivatedRouteSnapshot, Router, Routes } from '@angular/router';
import { Component, OnInit } from '@angular/core';
import { query } from '@angular/animations';
import { MyHttpService } from '../my-http.service';

@Component({
  selector: 'app-update-employee',
  templateUrl: './update-employee.component.html',
  styleUrls: ['./update-employee.component.css']
})
export class UpdateEmployeeComponent implements OnInit {
//selectcountry;
post:any[]=[];
id;
name:string='';
phoneno:string='';
departmentit:string='';
status:string='';
createdby:string='';
createddtm;
updateddtm;
updatedby:string='';
country={
  cid:'',
  cname:''
};
cname:string='';
cid:string='';





  constructor(private router:ActivatedRoute,private service:MyHttpService,private routes:Router) { }

  ngOnInit() {
this.Updateemployeedata();
this.GetAllcountry();
//this.updateddtm=Date.now();
  }

  Updateemployeedata(){
    this.router.queryParamMap.subscribe((query)=>{
      this.id=query.get('id');
      this.name=query.get('name');
      this.phoneno=query.get('phoneno');
      this.departmentit=query.get('departmentit');
      this.status=query.get('status');
      this.createdby=query.get('createdby');
      this.createddtm=query.get('createddtm');
      this.updatedby=query.get('updatedby');
      this.updateddtm=query.get('updateddtm');
      this.cid=query.get('cid');
      console.log("Country ID "+this.cid);
      this.cname=query.get('cname');
      console.log("Country name "+this.cname);
      this.country.cid=this.cid;
      this.country.cname=this.cname;
     })
  }

  GetAllcountry(){
    this.service.  GetAllcountry()
.subscribe((response)=>{
      console.log(response);
      this.post=(<any>response);
    })
  }

  onfinalupdate(){
    console.log("Country is "+this.country.cid);
    console.log("Country is "+this.country.cname);
    let obj1={
      "id":this.id,
       "name":this.name,
       "phoneno":this.phoneno,
       "departmentit":this.departmentit,
       "status":this.status,
       "createdby":this.createdby,
       "createddtm":this.createddtm,
       "updateddtm":Date.now(),
       "updatedby":this.updatedby,
        "country":{
         "cid":this.country.cid,
         "cname":this.country.cname
        }
        
    }

    this.service.Updateemployeedata(obj1)
    .subscribe((response)=>{
      console.log(response);
    this.routes.navigate(["/home"]);
      
    })

 
    
  }

}
