import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { ToastrService } from 'ngx-toastr';
import { MyHttpService } from '../my-http.service';
import { DialogService } from '../shared/dialog.service';

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.css']
})
export class HomeComponent implements OnInit {

  constructor(private service:MyHttpService,private route:Router,private toaster:ToastrService,
   private dialog:DialogService) { }
 post:any[]=[];
 //updateobj={};
 isRadioBtnClick=null;
 nameSearch:String='';

 updateobj={
  id:'',
  name:'',
  phoneno:'',
  departmentit:'',
  status:'',
  createdby:'',
  createddtm:'',
  updateddtm:'',
  updatedby:'',
   cname:'',
  cid:''
 };
  ngOnInit() {
  this.GetAllEmployee();
    }

    GetAllEmployee(){
      this.service.getAllEmployee().subscribe((response)=>{
        console.log(response);
        this.post=(<any>response);
      })
    }
    onRadioClick(item){
      this.isRadioBtnClick=item.id;
      console.log(item);
     // this.updateobj=item;
     this.updateobj={
       id:item.id,
       name:item.name,
       phoneno:item.phoneno,
       departmentit:item.departmentit,
       status:item.status,
       createdby:item.createdby,
       createddtm:item.createddtm,
       updateddtm:item.updateddtm,
       updatedby:item.updatedby,
        cname:item.country.cname,
       cid:item.country.cid 
       }
    }
  
    onAddEmpolyee(){
      this.route.navigate(['/addemployee']);
    }

    
    onUpdate(){
      if(this.RadioBtnValidation()){
      this.route.navigate(['/update'],{ queryParams:this.updateobj});
      }else{
        alert("Please Select Employee!!!! to Update ");
      }
     }

    onDelete(){
      if(this.RadioBtnValidation()){
        this.dialog.OpenConfirmDialog("Are you sure to delete this record?")
        .afterClosed()
        .subscribe((status)=>{
          console.log(status);

          if(status){
            this.service.Deleteemployeedata(this.updateobj.id)
            .subscribe((response)=>{
              console.log(response);
              this.GetAllEmployee();  
            this.toaster.success("Deleted Successfully!!");  })

          }
  })
     
      }else{
      alert("Please Select Employee!!!! to Delete ");
     // this.toaster.warning("Please Select Employee!!!! to Delete "); 
    }
    }

    onSuspend(){
      if(this.RadioBtnValidation()){
        this.dialog.OpenConfirmDialog("Are you sure to Suspend this record?")
        .afterClosed()
        .subscribe((status)=>{
          console.log(status);
          if(status){
      this.service.Suspendemployee(this.updateobj.id)
      .subscribe((response)=>{
        console.log(response);
        this.toaster.error("Suspend Successfully!!!!");
        this.GetAllEmployee();  })
      }
      });
    }else{
      alert("Please Select Employee!!!! to Suspend ");
    }
}

    onChangeStatus(){
      if(this.RadioBtnValidation()){
        this.dialog.OpenConfirmDialog("Are you sure to Change Status this record?")
        .afterClosed()
        .subscribe((status)=>{
          console.log(status);
          if(status){
      this.service.Changestatusemployee(this.updateobj.id)
      .subscribe((response)=>{
        console.log(response);
        this.GetAllEmployee();
        this.toaster.warning("Status change Successfully!!");
      })
    }
      })
    }else{
      alert("Please Select Employee!!!! to Change Status ");
    }
    }



    RadioBtnValidation(){
      if(this.isRadioBtnClick!=null){
        return true;
      }
      return false;
    }

   

  }
