import { HttpClient } from '@angular/common/http';
import { identifierModuleUrl } from '@angular/compiler';
import { Injectable } from '@angular/core';

@Injectable({
  providedIn: 'root'
})
export class MyHttpService {

  constructor(private http:HttpClient) { }

  url:string='http://localhost:8081/';

  postLogin(obj){
   return (this.http.post(this.url+"logincheck",obj));
  }

  getAllEmployee(){
    return (this.http.get(this.url+"getallemployee"));
  }
  GetAllcountry(){
    return (this.http.get(this.url+"getallcountry"));
 }

Addemployedata(obj){
 return (this.http.post(this.url+"addemployeetable",obj,{responseType:'text'}))
}
Updateemployeedata(obj1){
 return (this.http.put(this.url+"updateemployeedata",obj1,{responseType:'text'}))
}

Deleteemployeedata(id){
 return (this.http.delete(this.url+"deleteemployeetable"+"/"+id,{responseType:'text'}))
}
Suspendemployee(id){
 return (this.http.put(this.url+"employeestatussuspend"+"/"+id,null, {responseType:'text'}))
}
Changestatusemployee(id){
   return(this.http.get(this.url+"Changeemployeestatus"+"/"+id,{responseType:'text'}))
}
  
}
